        <el-dialog v-model="showDialog1" :title="展示" width="60%" :class="csscoll">
          <h1 style="font-size: 45px;">什么是雅乐</h1>
          <div class="show-content">
            <img src="../assets/朝代/雅乐.jpg" alt="" class="show-img" >
            <div class="show-text1">
              <p class="s-p1">雅乐，是指古代帝王祭祀天地、祖先及朝贺、宴享等大典所用的正乐。雅为正，儒家以它的音乐“中正和平”，歌词“典雅纯正”，故称之为“雅乐”。</p>
              <h3>历史背景</h3>
              <p>雅乐是先秦的古乐，《诗经》中的雅、颂，即是雅乐的诗歌。据说周武王建立周朝不久，就命周公姬旦制礼作乐，建立各种贵族生活中的礼仪和典礼音乐，使音乐为其王权统治服务。这一部分乐舞就是所谓的“雅乐”。它包含了远古图腾及巫术等宗教活动中的乐舞及祭祀音乐，也包含西周初期的民俗音乐。</p>
              <p>《周礼》所记载的西周和春秋时期的各种贵族礼仪，其中与雅乐有关的有：<strong>郊社：</strong>祭天地神明的祭典；<strong>尝禘：</strong>贵族祭其祖先的祭典；<strong>食飨：</strong>政治上外交上的宴会等；包括大飨、燕礼、大射、养老等等。<strong>乡射：</strong>乡里中官僚和地主们比射的集会；<strong>王师大献：</strong>战争胜利时举行的凯旋庆典；<strong>行军田役：</strong>用于军事演习性质的狩猎。</p>
            </div>
            <div class="show-text2">
              <h2 style="font-size: 35px;margin-bottom: 20px;">传播</h2>
              <div class="text2-chuanbo" style="overflow: hidden;">
                <img src="../assets/朝代/日本雅乐.jpg" alt="" style="float: left;width: 300px;height: 200px;margin-right: 40px;">
                <p style="width: 90%;margin-left: 50px;text-indent: 2em;">日本雅乐（ががく）最初从中国、朝鲜传入。日本大宝元年（701年）之前就设立了雅乐寮，雅乐寮古代日本乐舞的教育机构。教习古来的日本传统乐舞和外来音乐。日本雅乐与中国雅乐的不同之处还在于，日本雅乐是日本古代歌舞音乐的总称，它不仅指唐乐，而且还指高丽乐，以及日本古代的传统歌舞，例如东游、人长舞、久米舞、五节舞等，而中国的雅乐则专指供宫廷与宗教使用的祭祀典礼音乐。因此，必须注意的是中国雅乐与日本雅乐虽使用同一称谓。但它们的所指不同，是不同性质的两种音乐，真正与日本雅乐有血缘关系的不是唐代雅乐而是燕乐。</p>
                <p style="width: 90%;margin-left: 5%;text-indent: 2em;">朝鲜的雅乐，承袭中国夏商周三代的遗制，李朝曾有掌乐院及国乐司之设置，由都提调主持其事，下设雅乐师长、雅乐师、雅乐手等职称。李王家的雅乐，共分为宗庙音乐与文庙音乐二部，乐器的陈设有轩架和登歌之分，宫中宴乐举行时，使用的乐器大致相同。李朝掌乐院中的乐人，最盛时有七百人之多。</p>
                <p style="width: 90%;margin-left: 5%;text-indent: 2em;">越南雅乐（Nha Nhac），又名林邑乐，已被列入联合国人类口述和非物质遗产代表作里。
它在各种庆典仪式的开始和结束时演出，如周年庆典、宗教节日、加冕、丧葬或接见仪式等。越南众多的本土音乐种类中，只有宫廷雅乐堪称是具有全国影响、与东亚各国传统有着紧密联系的音乐。宫廷雅乐的演奏需要人数众多的乐师，有时需要伴舞和伴唱，歌舞演员服饰雍容华贵，衣料质地精良。大型乐队由乐鼓领头指挥，占据中心位置，其他包括多种打击乐器（排钟、锣、铃铛和木铃）和管弦乐器（笛子、芦笛、小号、筝和琵琶）。乐师必须非常专注，以跟随仪式的各个步骤。</p>
              </div>
            </div>
          </div>  
        </el-dialog>
        <el-dialog v-model="showDialog2" :title="展示" width="60%" :class="csscoll">
          <h1 style="font-size: 45px;">什么是燕乐</h1>
          <div class="show-content">
            <img src="../assets/朝代/燕乐.jpg" alt="" class="show-img">
            <div class="show-text1">
              <p class="s-p1">
                燕乐，又称宴乐，是隋唐至宋代的宫廷中饮宴时，提供娱乐欣赏的，艺术性很强的歌舞音乐。隋唐宫廷燕乐集中地反映了这一时期音乐文化的最高成就，它来源于汉族传统音乐的不断积累和汉魏以来传入的外来音乐，实质上是我国封建社会音乐文化的精华在长期积淀的基础上以及一定的政治、经济社会条件催化下的必然产物。
              </p>
              <h3>历史背景</h3>
              <p>周代已有所谓“燕乐”，即“房中乐”，为后妃在宫中所用，其歌词俱在《诗经》的《周南》、《召南》中。汉代宫廷中也有“房中乐”，这些都是雅乐的一部分，性质与唐、宋燕乐不同。广义的燕乐，如宋人沈括在《梦溪笔谈》中所说：“先王之乐为雅乐，前世新声为清乐，合胡部为燕乐。”是指汉族俗乐与外来音乐的总称。</p>
              <h3>应用场景</h3>
              <p>燕乐所使用的主要乐器有<strong>笙、笛、琵琶、箜篌、筚篥、羯鼓、方响</strong>等。安史之乱以后，宫廷音乐衰退，宫廷乐工多散落民间。宋统一后，虽设教坊，沿用唐代旧曲，但宫廷内外演奏的大曲只是唐时盛况的一部分，且规模大为缩减。宋代的大曲开始向歌舞剧演变，许多乐段逐渐变为曲牌，分散于词曲、戏曲、说唱和器乐中，大曲作为独立的音乐体裁则日渐消亡。南宋以后，音乐发展的主流转到市民艺术（戏曲、说唱、城市歌曲等）中，宫廷的燕乐和雅乐都失去了原有的发展势头。</p>
            </div>
            <div class="show-text2" style="margin-top: 50px;">
              <div class="text2-chuanbo" style="overflow: hidden;">
                <!-- <h3 style="font-size: 30px;">日本雅乐</h3> -->
                <img src="../assets/乐图/燕乐.jpg" alt="" style="float: left;width: 300px;height: 200px;margin-right: 50px;">
                <p style="height: 170px;width: 90%;margin-left: 50px;text-indent: 2em;">唐时燕乐曾传入日本，今日本宫内省雅乐队以及一些寺院还在演奏与唐大曲同名的古乐；朝鲜也保存有《春莺啭》（唐大曲曲名）等。不过经历了时代和生活环境的变迁，这些保存在异域的唐曲，已经很难看出和中国唐大曲还有多少相似之处。
平安时代，随着中日文化的友好交往传入日本，后发展成为日该国乐之部乐的基础，又称雅乐，它是中日人民情谊源远的象征。癸亥写于长安阿房宫，西京。</p>
              </div>
